.users {
  display: flex;
  margin-left: -1px;
  margin-bottom: 70px;

  p {
    color: $user-color;
    margin-top: 9px;
    font-family: $font-roboto;
    font-weight: 400;
    font-size: 1rem;
    line-height: 19px;
  }

  a {
    color: $user-link-color;
  }
}

.users__block {
  display: flex;
  margin-right: 28px;

  img {
    width: 38px;
    height: 38px;
    margin-right: -17px;
  }

  &--1 {
    z-index: 4;
  }
  &--2 {
    z-index: 3;
  }
  &--3 {
    z-index: 2;
  }
  &--4 {
    z-index: 1;
  }
}
